<template>
  <section>
    <el-row>
      <el-col :span="24">
        <span class="top-nav-link" @click="goBack">返回上一页</span>
      </el-col>
      <!-- <el-col class="case-info" v-for="node in nodeList" :key="node.title">
      <span class="title">【{{node.title}}】</span> {{node.data}}
      </el-col> -->
      <el-col class="case-info">
        <span class="title">【主诉】</span> {{pageInfo.chiefComplaint}}
        <span class="title">【现病史】</span> {{pageInfo.historyPresent}}
        <span class="title">【既往史】</span> {{pageInfo.historyPast}}
        <span class="title">【过敏史】</span> {{pageInfo.historyAllergic}}
        <span class="title">【体格检查】</span> {{pageInfo.physicalExamination}}
        <span class="title">【中医病名】</span> {{pageInfo.chineseMedicineName}}
        <span class="title">【西医病名】</span> {{pageInfo.westernMedicineName}}
        <span class="title">【证型】</span> {{pageInfo.dialecticalName}}
        <span class="title">【治法】</span> {{pageInfo.cureName}}
        <span class="title">【处方名称】</span> {{chuFangInfo.drugName}}
        <span class="title">【药材】</span>
        <i v-for="item in caoYaoItem" :key="item.drugCode">{{item.drugName}} {{item.quanitity}}g  ;</i>
        <span class="title">【付数】</span> {{chuFangInfo.usageCount}}
        <span class="title">【用法】</span> {{chuFangInfo.fangjiUsage}}
        <span class="title">【医嘱】</span> {{chuFangInfo.advice}}
        <span class="title">【治疗评价】</span> {{evaluationInfo}}
        <span class="title">【评价备注】</span> {{pageInfo.evaluationRemark}}
        <!-- <span class="title">【检验】</span>

        <el-table
      :data="tableData1"  style="width: 100%">
      <el-table-column
        prop="detailItemName"
        label="检验项目"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址">
      </el-table-column>
      </el-table> -->

      </el-col>
    </el-row>
    <el-row  class="toolbar">
        <el-col :span="12">
          <el-button size="medium" type="primary" @click="handleCaseCompare" >比较病例</el-button>
          <!-- <el-button type="primary" disabled>复制病例</el-button> -->
          <el-button size="medium" type="primary" @click="handleOpenEvaluation">治疗评价</el-button>
        </el-col>
        <el-col :span="12" align="right">
          <el-button size="medium" type="primary" @click="pagePrint" >打印病例</el-button>
          <el-button size="medium" type="primary" @click="chufangPrint">打印处方</el-button>
        </el-col>
    </el-row>
    <el-dialog
      title="比较病例" center
      :visible.sync="caseCompartShow"
      width="80%">
      <el-row>
        <el-col :span="12">
          <ul class="case-compart">
            <li> <span class="title">项目</span> <span>{{ caseData1.registrationNum }}</span></li>
            <li> <span class="title">主诉</span> <span>{{ caseData1.chiefComplaint }}</span></li>
            <li> <span class="title">现病史</span> <span> {{ caseData1.historyPresent }} </span></li>
            <li> <span class="title">既往史</span> <span>{{ caseData1.historyPast }}</span></li>
            <li> <span class="title">过敏史</span> <span>{{ caseData1.historyAllergic }}</span></li>
            <li> <span class="title">体格检查</span> <span>{{ caseData1.physicalExamination }}</span></li>
            <li> <span class="title">中医病名</span> <span>{{ caseData1.chineseMedicineName }}</span></li>
            <li> <span class="title">西医病名</span> <span>{{ caseData1.westernMedicineName }}</span></li>
            <li> <span class="title">证型</span> <span>{{ caseData1.dialecticalName }}</span></li>
            <li> <span class="title">治法</span> <span>{{ caseData1.cureName }}</span></li>
            <li> <span class="title">处方名称</span> <span>{{ caseData1.drugName }}</span></li>
            <li> <span class="title">药材</span> <span> <i v-for="item in caoYaoItem1" :key="item.drugCode">{{item.drugName}} {{item.quanitity}}g  ;</i></span></li>
            <li> <span class="title">付数</span> <span>{{ caseData1.usageCount }}</span></li>
            <li> <span class="title">用法</span> <span>{{ caseData1.fangJiUsage }}</span></li>
            <li class="li-border-last"> <span class="title">医嘱</span> <span>{{ caseData1.advice }}</span></li>
          </ul>
        </el-col>
        <el-col :span="12">
          <ul class="case-compart li-border-right">
            <li> <span> {{ caseData2.registrationNum }}</span></li>
            <li> <span> {{ caseData2.chiefComplaint }}</span></li>
            <li> <span> {{ caseData2.historyPresent }} </span></li>
            <li> <span> {{ caseData1.historyPast }}</span></li>
            <li> <span> {{ caseData2.historyAllergic }}</span></li>
            <li> <span> {{ caseData2.physicalExamination }}</span></li>
            <li> <span> {{ caseData2.chineseMedicineName }}</span></li>
            <li> <span> {{ caseData2.westernMedicineName }}</span></li>
            <li> <span> {{ caseData2.dialecticalName }}</span></li>
            <li> <span> {{ caseData2.cureName }}</span></li>
            <li> <span> {{ caseData2.drugName }}</span></li>
            <li> <span> <i v-for="item in caoYaoItem2" :key="item.drugCode">{{item.drugName}} {{item.quanitity}}g  ;</i></span></li>
            <li> <span> {{ caseData2.usageCount }}</span></li>
            <li> <span> {{ caseData2.fangJiUsage }}</span></li>
            <li class="li-border-last"> <span> {{ caseData2.advice }}</span></li>
          </ul>
        </el-col>
        <el-col class="toolbar" align='center'>
          <el-button size="medium" @click="caseCompartShow = false">关 闭</el-button>
        </el-col>
      </el-row>
    </el-dialog>
    <el-dialog
      title="治疗评价" center
      :visible.sync="evaluationShow">
      <el-row>
        <el-col>
          <el-form :model= 'evaluationData' label-width="80px" :rules="rules" ref="ruleForm">
            <el-form-item label="评价:" prop="evaluationCode">
              <el-select v-model="evaluationData.evaluationCode" placeholder="请选择治疗评价" style="width: 100%">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="备注:">
              <el-input v-model="evaluationData.evaluationRemark"></el-input>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col class="toolbar" align='center'>
          <el-button type="primary" size="medium" @click="handleSetEvaluation">保 存</el-button>
          <el-button size="medium" @click="evaluationShow = false">取 消</el-button>
        </el-col>
      </el-row>
    </el-dialog>
    <el-dialog
      title="选择病例" center
      :visible.sync="caseSelectShow"
      width="60%">
      <el-row>
        <el-col>
          <el-table
            :data="caseList"
            ref="caseList"
            @selection-change="handleCaseChange"
            style="width: 100%">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="mecidalNum"
              label="就诊号">
            </el-table-column>
            <el-table-column
              prop="registrationNum"
              label="挂号序号">
            </el-table-column>
            <el-table-column
              prop="deptName"
              label="科室">
            </el-table-column>
            <el-table-column
              prop="userName"
              label="医生">
            </el-table-column>
            <el-table-column
              prop="onSetDate"
              label="就诊时间"
              min-width="160">
            </el-table-column>
          </el-table>
        </el-col>
        <el-col class="toolbar" align='center'>
          <el-button type="primary" size="medium" @click="handleCompare">比 较</el-button>
          <el-button size="medium" @click="caseSelectShow = false">关 闭</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </section>
</template>
<script>
import {
  getEmpBasicList,
  getEmpBasicInfo,
  evaluationCodeRemark, // 治疗评价
  printChuFang, // 患者中药处方
  getEmpBasicMainEntyTable, // 患者详情
  getYiCunCheckItemList, // 检查/检验数据
} from '../../api'
export default {
  data(){
    return{
      tableData1: [{data:1}],
      tableData2: [],
      rules:{
        evaluationCode: [{  required: true, message: '请选择评价', trigger: 'change' }]
      },
      info: '',
      evaluationShow: false, // 治疗评价
      evaluationData: { // 治疗评价数据
        evaluationCode: '',
        evaluationCodeRemark: ''
      },
      options: [ // 治疗评价
        {
          label: '有效',
          value: '10001'
        },
        {
          label: '好转',
          value: '10002'
        },
        {
          label: '痊愈',
          value: '10003'
        },
        {
          label: '未愈',
          value: '10004'
        }
      ],
      pageInfo: {}, // 患者详情
      chuFangInfo: {}, // 处方信息
      nodeList: [],// 病例信息
      registrationNum: '',
      caoYaoItem: [] , // 草药数据
      mecidalNum: '', // 就诊号
      caseSelectShow: false, // 比较选择窗口
      caseList: [], // 病例列表数据
      caseSelectList: [],
      caseCompartShow: false, // 比较窗口
      caseData1: {}, // 待比较数据1
      caseData2: {}, // 待比较数据2
      caoYaoItem1: [], // 草药列表1
      caoYaoItem2: [], // 草药列表2
    }
  },
  computed: {
    evaluationInfo () {
      let info = ''
      this.options.forEach(element => {
        if (element.value == this.pageInfo.evaluationCode) {
          info = element.label
        }
      })
      return info
    }
  },
  methods:{
    pagePrint(){ // 打印
      window.open('#/printPage?id=' + this.$route.query.id,'_blank')
    },
    handleCompare () {
      if (this.caseSelectList.length != 2) {
        this.$message.error('请选择两条数据比较！')
      }else{
        this.caseCompartShow = true
        getEmpBasicMainEntyTable( { registrationNum: this.caseSelectList[0].registrationNum } ).then(res => {
          if (res.data.code == '0') {
            this.caseData1 = res.data.data[0]
            printChuFang({ drugFlag: 0, empBasicMainID: res.data.data[0].empBasicMainID, }).then(res => {
              if(res.data.code == '0') {
                this.caoYaoItem1 = res.data.data.fangJiDetailList
              }
            })
          }
        })
        getEmpBasicMainEntyTable( { registrationNum: this.caseSelectList[1].registrationNum } ).then(res => {
          if (res.data.code == '0') {
            this.caseData2 = res.data.data[0]
            printChuFang({ drugFlag: 0, empBasicMainID: res.data.data[0].empBasicMainID, }).then(res => {
              if(res.data.code == '0') {
                this.caoYaoItem2 = res.data.data.fangJiDetailList
              }
            })
          }
        })
      }
    },
    handleCaseChange (val) {
      this.caseSelectList = val
    },
    handleCaseCompare () {
      this.caseSelectShow = true
      this.getCaseList()
    },
    // 打开治疗评价页面
    handleOpenEvaluation () {
      this.evaluationShow = true
    },
    // 设置治疗评价
    handleSetEvaluation () {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.saveEvaluation()
        } else {
          return false
        }
      })
    },
    saveEvaluation () {
      evaluationCodeRemark({
        evaluationCode: this.evaluationData.evaluationCode,
        evaluationRemark: this.evaluationData.evaluationRemark,
        registrationNum: this.registrationNum,
        lunAfterImg: ''
      }).then(res => {
        if (res.data.code === '0') {
          this.$message.success('评价成功！')
          this.getCaseData()
          this.evaluationShow = false
        } else {
          this.$message.error('评价失败！')
        }
      })
    },
    getCaseList () {
      getEmpBasicList({ mecidalNum: this.mecidalNum}).then(res => {
        if (res.data.code == '0') {
          this.caseList = res.data.data
        }
      })
    },

    chufangPrint(){
      sessionStorage.setItem('current_registrationNum',this.registrationNum)
      window.open('#/printPrescription?id=' + this.registrationNum,'_blank')
    },
    getCaseData () {
      let id = this.$route.query.id || 0
      this.registrationNum = id
      this.mecidalNum = this.$route.query.mn
      let eid = sessionStorage.getItem('eid')
      if(!id || !eid || ! this.mecidalNum){
        this.$message.warning('无效访问 请正常进入!')
        return
      }
        getEmpBasicMainEntyTable({
          registrationNum: id
        }).then(res=>{
          if(res.data.code=='0'){
            this.pageInfo = res.data.data[0]
          }
        })
        printChuFang({ // 中医处方
          drugFlag: 0,
          empBasicMainID: eid,
        }).then(res=>{
          if(res.data.code == '0'){
            this.chuFangInfo = res.data.data
            this.caoYaoItem = res.data.data.fangJiDetailList
          }
        })
    }
},
  mounted(){
    this.getCaseData()
      // getYiCunCheckItemList({empBasicMainID: eid}).then(res=>{
      //   if(res.data.code == '0'){
      //     this.tableData1 = res.data.data
      //   }
      // })
    //   getEmpBasicInfo({
    //   registrationNum: id,
    //   drugFlag: 0
    //   }).then(res=>{
    //     if(res.data.code == '0') return
    //   let str = res.data.data[0].returnValueStr
    //   let reg = /(【[\u4e00-\u9fa5]*】[^【]+)/g
    //   let arr =str.match(reg)
    //   let tpl = ''
    //   let p = document.querySelector('#result')
    //   arr.forEach(element => {
    //     let temp = element.split('】')
    //     let span = null
    //     this.nodeList.push({
    //       title: temp[0].substring(1),
    //       data: temp[1] || '',
    //     })
    //   })
    // })
    // }else{
    //   this.$message.warning('无效访问 请正常进入!')
    // }

  }
}
</script>
<style scoped>
.case-info{
  font-size: 14px;
  color: #555;
  line-height: 20px;
}
.case-info .title{
  font-size: 15px;
  color: #333;
}
.case-info .title::before{
  display: block;
  content: ' ';
  width: 100%;
  height: 12px;
}
.case-info .el-table{
  margin: 20px 6px;
}
.case-info .el-table i{
  color: #3E85FA;
  font-size: 22px;
  cursor: pointer;
}
.case-compart li{
  display: flex;
  line-height: 28px;
  border-width: 0;
  border-top-width: 1px;
  border-left-width: 1px;
  border-color: #333;
  border-style: solid;
  overflow: hidden;
}
.case-compart li span{
  display: inline-block;
  padding-left: 8px;
  white-space: pre;
}
.case-compart li .title{
  width: 80px;
  border-right: 1px solid #333;
}
.li-border-right{
  border-right: 1px solid #333;
}
.li-border-last{
  border-bottom: 1px solid #333 !important;
}
</style>




